.nav {
    display: flex;
    border-bottom: 1px solid gray;
    background: white;
    padding: 0 10px;
}
.square {
    width: 25px;
    height: 25px;
    background-color: #44ff00;
}
.link {
    width: 100px;
    border-right: 1px solid gray;
    padding: 10px;
    text-align: center;
    position: relative;
    cursor: pointer;
}

.menu {
    opacity: 0;
    position: absolute;
    top: 40px;
left: 0;
    background: #ededed;
    border: 1px solid gray;
    padding: 10px;
    text-align: center;
    transition: all 1000ms ease;
}

.menu.open {
    opacity: 1;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}